<template>
  <div class="wrapper">
      <div class="container">
        <ChatA :msgArr="msgArr" @pushMsg="pushMsg" @showModal="showModal"/>
        <ChatB :msgArr="msgArr" @pushMsg="pushMsg" @showModal="showModal"/>
      </div>
      <ImgAdder :modalOpened="modalOpened" :chatInfo="chatInfo" @pushImgMsg="pushImgMsg" @cancleImg="cancleImg"/>
  </div>
</template>

<script>
import ChatA from '@/components/ChatA.vue'
import ChatB from '@/components/ChatB.vue'
import ImgAdder from '@/components/ImgAdder.vue'

export default {
    data() {
      return{
        msgArr: [],
        modalOpened: false,
        chatInfo: ''
      }
    },
    methods: {
      pushMsg(obj){
        this.msgArr.push(obj)
      },
      showModal(info){
        this.modalOpened = !this.modalOpened  
        this.chatInfo = info
      },
      pushImgMsg(obj){
        this.msgArr.push(obj)
        this.modalOpened = !this.modalOpened  
      },
      cancleImg(obj){
        this.modalOpened = !this.modalOpened
      }
    },
    components: {
    ChatA,
    ChatB,
    ImgAdder
}
}
</script>

<style>
  .wrapper{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #98C0FF;
    width: 100%;
    height: 100vh;
    position: relative;
  }

</style>