<template>
  <div class="chat__wrapper">
    <div class="chat__header">
        <img src="@/assets/img/chatB-avatar.svg" alt="avatar">
        <div class="chat__header-info">
            <h2>Евгений</h2>
            <p v-if="msg==''">Онлайн</p>
            <p v-else>Печатает...</p>
        </div>
    </div>
    <div class="chat__messages">
        <div class="my-message" :class="{'not-msg': msg.chat == 'A'}" v-for="msg in msgArr" :key="msg.text">
        <p :class="{'not': msg.chat == 'A'}"><img :src="msg.imgUrl" alt="">{{msg.text}}</p>
        <span>{{msg.time}}</span>
        </div>
    </div>
    <div class="chat__input">
        <input type="text" placeholder="Написать сообщение..." @keypress.enter="msg != '' ?renderMsgB():''" v-model="msg">
        <button @click="this.$emit('showModal', 'B')"><img src="@/assets/img/send-img.svg" alt="img"></button>
    </div>
  </div>
</template>

<script>
export default {
    props: ['msgArr'],
    data(){
        return{
            msg: '',
            msgArr2: []
        }
    },

    methods:{
        renderMsgB(){
            let obj = {
                chat: 'B',
                imgUrl: '',
                text: this.msg, 
                time: `${new Date().getHours()}:${new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes()}`
                }
            this.$emit('pushMsg', obj)
            this.msg = ''
        }
    }
}
</script>

