<template>
  <div class="img-adder__wrapper" v-show="modalOpened">
    <div class="img-adder__block">
       <h2>Отправить картинку</h2> 
        <input type="text" v-model="imgUrl" placeholder="URL">
        <input type="text" v-model="msg" placeholder="Комментарий">
        <div class="img-adder__btns">
            <button class="red" @click="cancleImg">ОТМЕНА</button>
            <button @click="msg != '' && imgUrl != '' ? renderImgMsg() : ''">ОТПРАВИТЬ</button>
        </div>
    </div>
  </div>
</template>

<script>
export default {
    props: ['modalOpened', 'chatInfo'],
    data(){
        return{
            msg: '',
            imgUrl: '',
        }
    },
    methods:{
        renderImgMsg(){
            let obj = {
                chat: this.chatInfo,
                imgUrl: this.imgUrl,
                text: this.msg, 
                time: `${new Date().getHours()}:${new Date().getMinutes() < 10 ? '0' + new Date().getMinutes() : new Date().getMinutes()}`
            }
            this.$emit('pushImgMsg', obj)
            this.msg = ''
            this.imgUrl = ''
        },
        cancleImg(){
            this.msg = ''
            this.imgUrl = ''
            this.$emit('cancleImg', 'closed')
        }
    }
}
</script>

<style>
.img-adder__wrapper{
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.35);
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
}
.img-adder__block{
    width: 312px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 24px;
    background: #F2F7FF;
    border-radius: 28px;
    gap: 16px;
}
.img-adder__block > h2{
    color: #1C1B1F;
    font-size: 24px;
    line-height: 32px;
    font-weight: 400;
}
.img-adder__block > input {
    border: none;
    outline: none;
    background: #E7E0EC;
    padding: 15px 20px;
    font-size: 17px;
    line-height: 24px;
}
.img-adder__block > input::placeholder{
    color: #49454F;
    font-size: 16px;
    line-height: 24px;
}

.img-adder__btns{
    margin-top: 5px;
    align-self: end;
    display: flex;
    gap: 15px;
}
.img-adder__btns button{
    color: #6750A4;
    font-size: 14px;
    line-height: 20px;
    font-family: rm;
    padding: 10px;
    cursor: pointer;
}
.red{
    color: #CF1B1B !important;
}
</style>